
'use client';

import { useState, useEffect } from 'react';
import { useForm, Controller, useWatch } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Header } from '@/components/layout/Header';
import { useMockDatabase } from '@/lib/data';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { Checkbox } from '@/components/ui/checkbox';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Save } from 'lucide-react';
import type { Volunteer, UniversalRole } from '@/types';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { Switch } from '@/components/ui/switch';
import { Separator } from '@/components/ui/separator';

const preferencesSchema = z.object({
  preferredRoleIds: z.array(z.string()).default([]),
  servingFrequency: z.enum(['weekly', 'bi-weekly', 'monthly', 'as-needed']),
  serveWithFamily: z.boolean().default(false),
  familyManagementEnabled: z.boolean().default(false),
});

type PreferencesFormValues = z.infer<typeof preferencesSchema>;

export default function MyPreferencesPage() {
  const db = useMockDatabase();
  const { toast } = useToast();
  const { isLoggedIn, isAdmin } = useAuth();
  const router = useRouter();
  
  const [volunteer, setVolunteer] = useState<Volunteer | null>(null);
  const [universalRoles, setUniversalRoles] = useState<UniversalRole[]>([]);
  const [isSaving, setIsSaving] = useState(false);
  
  const form = useForm<PreferencesFormValues>({
    resolver: zodResolver(preferencesSchema),
    defaultValues: {
        preferredRoleIds: [],
        servingFrequency: 'as-needed',
        serveWithFamily: false,
        familyManagementEnabled: false,
    }
  });

  const familyManagementEnabled = useWatch({
    control: form.control,
    name: 'familyManagementEnabled',
  });

  useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (isAdmin) {
      router.push('/admin');
    }
  }, [isLoggedIn, isAdmin, router]);

  useEffect(() => {
    if (isLoggedIn) {
      const vol = db.getVolunteersSync().find(v => v.id === db.MOCK_VOLUNTEER_ID);
      if (vol) {
        setVolunteer(vol);
        form.reset(vol.preferences);
      }
      db.getUniversalRoles().then(setUniversalRoles);
    }
  }, [isLoggedIn, db, form]);

  const onSubmit = async (data: PreferencesFormValues) => {
    if (!volunteer) return;
    setIsSaving(true);
    try {
      await db.updateVolunteerPreferences(volunteer.id, data);
      toast({
        title: 'Preferences Saved',
        description: 'Your serving preferences have been updated.',
      });
      // This is a hacky way to force a re-render of the header
      window.dispatchEvent(new Event('storage'));
    } catch (e) {
      toast({
        variant: 'destructive',
        title: 'Error',
        description: 'Could not save your preferences. Please try again.',
      });
    } finally {
      setIsSaving(false);
    }
  };

  if (!isLoggedIn || isAdmin || !volunteer) {
    return null; // Or a loading spinner
  }

  return (
    <div className="flex min-h-screen w-full flex-col bg-muted/40">
      <Header />
      <main className="flex-1 p-4 md:p-8">
        <div className="max-w-3xl mx-auto space-y-6">
          <div>
            <h1 className="text-3xl font-bold font-headline tracking-tight">My Preferences</h1>
            <p className="text-muted-foreground mt-1">
              Let us know how you'd like to serve. This helps us find the best fit for you.
            </p>
          </div>

          <form onSubmit={form.handleSubmit(onSubmit)}>
            <Card>
              <CardHeader>
                <CardTitle>Preferred Roles</CardTitle>
                <CardDescription>Select the roles you enjoy the most. You can select more than one.</CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-2 md:grid-cols-3 gap-4">
                <Controller
                  control={form.control}
                  name="preferredRoleIds"
                  render={({ field }) => (
                    <>
                      {universalRoles.map(role => (
                        <div key={role.id} className="flex items-center space-x-2">
                          <Checkbox
                            id={`role-${role.id}`}
                            checked={field.value?.includes(role.id)}
                            onCheckedChange={(checked) => {
                              return checked
                                ? field.onChange([...(field.value || []), role.id])
                                : field.onChange(field.value?.filter(id => id !== role.id));
                            }}
                          />
                          <Label htmlFor={`role-${role.id}`} className="font-normal">{role.name}</Label>
                        </div>
                      ))}
                    </>
                  )}
                />
              </CardContent>
            </Card>

            <Card className="mt-6">
              <CardHeader>
                <CardTitle>Serving Frequency</CardTitle>
                <CardDescription>How often would you ideally like to be scheduled?</CardDescription>
              </CardHeader>
              <CardContent>
                 <Controller
                    control={form.control}
                    name="servingFrequency"
                    render={({ field }) => (
                        <RadioGroup
                          onValueChange={field.onChange}
                          value={field.value}
                          className="grid grid-cols-2 gap-4"
                        >
                            <div className="flex items-center space-x-2">
                                <RadioGroupItem value="weekly" id="weekly" />
                                <Label htmlFor="weekly">Weekly</Label>
                            </div>
                             <div className="flex items-center space-x-2">
                                <RadioGroupItem value="bi-weekly" id="bi-weekly" />
                                <Label htmlFor="bi-weekly">Every 2 Weeks</Label>
                            </div>
                             <div className="flex items-center space-x-2">
                                <RadioGroupItem value="monthly" id="monthly" />
                                <Label htmlFor="monthly">Monthly</Label>
                            </div>
                             <div className="flex items-center space-x-2">
                                <RadioGroupItem value="as-needed" id="as-needed" />
                                <Label htmlFor="as-needed">As Needed</Label>
                            </div>
                        </RadioGroup>
                    )}
                 />
              </CardContent>
            </Card>

            <Card className="mt-6">
              <CardHeader>
                <CardTitle>Family Management</CardTitle>
                <CardDescription>Coordinate scheduling with your family members.</CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                  <FormField
                    control={form.control}
                    name="familyManagementEnabled"
                    render={({ field }) => (
                        <div className="flex items-center justify-between rounded-lg border p-4">
                            <div>
                                <h3 className="text-base font-medium">Enable Family Features</h3>
                                <p className="text-sm text-muted-foreground">
                                    Adds the "My Family" page to manage members and availability.
                                </p>
                            </div>
                            <FormControl>
                                <Switch
                                checked={field.value}
                                onCheckedChange={field.onChange}
                                />
                            </FormControl>
                        </div>
                    )}
                    />
                
                {familyManagementEnabled && (
                   <>
                    <Separator />
                    <Controller
                        control={form.control}
                        name="serveWithFamily"
                        render={({ field }) => (
                            <div className="flex items-center space-x-2">
                            <Checkbox
                                id="serveWithFamily"
                                checked={field.value}
                                onCheckedChange={field.onChange}
                            />
                            <Label htmlFor="serveWithFamily" className="font-normal">
                                I prefer to serve with family members when possible.
                            </Label>
                            </div>
                        )}
                        />
                   </>
                )}

              </CardContent>
            </Card>

            <div className="flex justify-end mt-6">
                <Button type="submit" disabled={isSaving}>
                    {isSaving ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Save className="mr-2 h-4 w-4" />}
                    Save Preferences
                </Button>
            </div>
          </form>
        </div>
      </main>
    </div>
  );
}
